<template>
  <div class="home">
    <section class="section">
      <nav class="nav">
        <div class="container">
          <input 
            type="text" class="input is-large" 
            placeholder="Buscar Canción" 
            v-model="searchQuery"
            @keyup.enter="search"
          >

          <a 
            v-bind:class="`button is-primary is-large ${ isLoading ? 'is-loading' : ''}`" 
            style="margin:10px 20px 10px 0px;" 
            v-on:click="search"
          >Buscar</a>
          <a class="button is-danger is-large" style="margin:10px 10px 10px 0px;"> &times; </a>
          
        </div>
      </nav>
      <div class="container">
        <p>
          <small> {{ searchMessage }}  </small>
        </p>
      </div>
      <transition name="move" mode="out-in">
        <pm-notification v-show="showNotification">
          <p slot="body">No se encontraron resultados</p>
        </pm-notification>
      </transition>
      <transition name="loading" mode="out-in">
        <pm-loader v-show="isLoading"></pm-loader>
      </transition>
      <div class="container results" v-show="!isLoading" style="margin-top:20px;">
        <div class="columns is-multiline">
          <div class="column is-one-third" v-for="track in tracks" :key="track.id">
            <transition name="loading" appear>
              <pm-track 
                v-blur="track.preview_url"
                v-bind:track="track" 
                v-on:select="setSelectedTrack"
                v-bind:class="{ 'is-active' : track.id  === selectedTrack }"
              ></pm-track>
            </transition>
          </div>
        </div>
      </div>
    </section>

    
  </div>
</template>

<script>
import PmTrack from '@/components/Track.vue'
import PmLoader from '@/components/shared/Loader.vue'
import PmNotification from '@/components/shared/Notification.vue'


import trackService from '@/services/track'



export default {
    data() {
      return {
        searchQuery: '',
        tracks: '',
        isLoading: false,
        selectedTrack: '',
        showNotification: false,
      }
  },
  components: {
    PmTrack,
    PmLoader,
    PmNotification
  },
  methods: {
      search() {
        if(!this.searchQuery) { return }

        this.isLoading = true

        trackService.search(this.searchQuery)
          .then(res => {
            this.showNotification = res.tracks.total === 0
            this.tracks = res.tracks.items
            this.isLoading = false
          })
          .catch((error) => {
            console.log(error)
          })
      },
    setSelectedTrack(id) {
      this.selectedTrack = id
      
    }
    },
    computed: {
      searchMessage() {
        return `Encontrados ${this.tracks.length}`
      }
    },
    watch: {
      showNotification() {
        if (this.showNotification) {
          setTimeout(() => {
            this.showNotification = false
          }, 3000)
        } 
      }
    }
}
</script>
<style scoped>
  .is-active {
    border: 2px solid #00d1b2;
    transition: all .2s ease-in-out;
  }

.move-enter-active, .move-leave-active {
  transform: translateY(0);
  opacity: 1;
  transition: all .3s ease-in-out;
}


  .move-enter, .move-leave-to {
  opacity: 0;
  transform:translateY(-30px);
  transition: all .3s ease-in-out;
}

.loading-enter-active, .loading-leave-active {
  opacity: 1;
   transition: all .2s ease-in-out;
}
.loading-enter, .loading-leave-to {
  opacity: 0;
   transition: all .5s ease-in-out;
}



</style>

