<template>
    <div class="container" v-if="track && track.id">
        <div class="columns">
            <div class="column is-3 has-text-centered">
                <figure class="image">
                    <img :src="track.album.images[1].url" alt="">
                </figure>
                <p>
                    <button class="is-primary button" style="margin:10px;">
                        <span class="icon" @click="selectTrack"> ♫ </span>
                    </button>
                </p>
            </div>
            <div class="column is-8">
                <div class="panel">
                    <div class="panel-heading">
                        <h1 class="title">
                            {{ trackTitle }}
                        </h1>
                    </div>
                    <div class="panel-block">
                        <article class="media">
                            <div class="media-content">
                                <div class="content">
                                    <ul>
                                        <li>
                                            <strong>
                                                {{ track.album.type }} : &nbsp;
                                            </strong>
                                            <span>
                                                {{ track.album.name }}
                                            </span>
                                        </li>
                                        <li>
                                            <strong>
                                                Duration  : &nbsp;
                                            </strong>
                                            <span>
                                                {{ track.duration_ms | ms-to-mm }}
                                            </span>
                                        </li>
                                        <li>
                                            <strong>
                                                rank : &nbsp;
                                            </strong>
                                            <span>
                                                {{ track.popularity }}
                                            </span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <nav class="level">
                                <div class="level-left">
                                    <a class="level-item"></a>
                                </div>
                            </nav>
                        </article>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import trackMixin from '@/mixins/track.js'
// import trackService from '@/services/track.js'
import { mapState, mapActions, mapGetters } from 'vuex'
export default {
    mixins: [
        trackMixin
    ],
    computed: {
        ...mapState(['track']),
        ...mapGetters(['trackTitle'])
    },
    created() {
        const id = this.$route.params.id

        if(!this.track || !this.track.id || this.track.id != id) {
            this.getTrackById({ id })
                .then(() => {
                    console.log('track loaded');
                    
                })
        }
    },
    methods: {
        ...mapActions(['getTrackById'])
    }
}
</script>

<style scoped>
    .columns {
        margin:20px;
    }

    ul li {
        margin:20px;
    }
</style>
